@import url(https://use.fontawesome.com/releases/v5.0.7/css/all.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{background-color:rgba(108,99,255,0.2)}.app-body{display:grid;grid-template-columns:1fr 8fr}

.table{display:grid;grid-template-columns:1fr 1fr 1fr;margin:10px;grid-gap:10px;gap:10px}.table-item{padding:10px;text-transform:capitalize;background-color:#fff;border-radius:1em;text-decoration:none;color:#000}.table-item:nth-child(odd){background:#eee}.table-item img{width:100%;height:auto;border-radius:1em}.table-item i{color:#6c63ff}.not-found{display:flex;justify-content:center;align-items:center}.not-found h1{position:relative;left:-100px;top:100px;z-index:1;font-size:4em}.not-found-image{width:50%;height:80%;position:absolute;top:100px;right:50%;transform:translateX(50%);background:transparent}

.sidebar{display:flex;flex-direction:column;text-transform:capitalize;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;justify-self:center;position:-webkit-sticky;position:sticky;top:100px}.sidebar .categories{margin-left:-20px;list-style:none;overflow:auto;height:60vh;scrollbar-width:none;-ms-overflow-style:none}.sidebar .categories::-webkit-scrollbar{display:none}.sidebar .categories li{padding:10px}.sidebar .categories li:hover{color:rgba(108,99,255,0.5);cursor:pointer}.sidebar .categories .selected{font-weight:bold;color:#6c63ff}.sidebar .reset-button{margin-top:20px;font-size:1.3em;border-radius:20px;color:#6c63ff;padding:5px 0}.sidebar .reset-button:hover{background-color:rgba(108,99,255,0.2);cursor:pointer}.icon{position:relative;left:50%;-webkit-animation:pulse 1s ease-in-out infinite alternate;animation:pulse 1s ease-in-out infinite alternate;color:rgba(108,99,255,0.5)}@-webkit-keyframes pulse{from{top:-20px}to{top:0px}}@keyframes pulse{from{top:-20px}to{top:0px}}

*{border:0;outline:0}.search{display:flex;position:relative;top:0;overflow:hidden;width:60px;height:60px;margin:10px;border-radius:100px;transition:width 1s ease-in-out}.search-input{position:absolute;top:0;bottom:0;right:0;font-weight:bold;opacity:0;background-color:#6c63ff}.search-input::-webkit-input-placeholder{color:white}.search-input:-ms-input-placeholder{color:white}.search-input::-ms-input-placeholder{color:white}.search-input::placeholder{color:white}.search-button{font-size:1.5em;background-color:#fff;margin:10px;width:40px;height:40px;border-radius:100px;margin-right:auto;z-index:1;color:#6c63ff}.search:focus-within{width:50%}.search:focus-within .search-input{opacity:1;width:100%;text-indent:60px;color:#fff}

.nav{display:flex;justify-content:space-between;align-items:center;background-color:#fff;position:-webkit-sticky;position:sticky;top:0}.nav-title{display:flex;align-items:center;padding:10px}.nav-items{display:flex;list-style:none}.nav-items li{padding:10px}.nav-items li a{text-decoration:none;color:#000}.nav-items li a:hover{color:#6c63ff}.newspaper-image{width:80px;height:auto}

